.buddh__header {
    display: flex;
}
        /* for all the content styling */
.buddh__header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    margin-right: 5rem;
}
        /* for h1 tag styling */
.buddh__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: 0.04em;
}
        /* below one for paragraph styling  */
.buddh__header-content p {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 25px;
    color: var(--color-text);

    margin-top: 1.5rem;
}
        /* input box */
.buddh__header-content__input {
    width: 100%;
    margin: 2rem 0 1rem;

    display: flex;
}
        /* under the input box styling section */
.buddh__header-content__input input {
    flex: 2;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    border: 2px solid var(--color-footer);
    background: var(--color-footer);
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    padding: 0 1rem;
    outline: none;
    color: #fff;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}
        /* for button styling */
.buddh__header-content__input button {
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 20px;
    line-height: 27px;
    color: #FFFFFF;
    cursor: pointer;
    outline: none;
    padding: 0 1rem;
    border: none;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}
        /* styling people img + para beside it */
.buddh__header-content__people {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2rem;
}
        /* styling people image */
.buddh__header-content__people img{
    width: 181.79px;
    height: 38px;
}
        /* styling paragraph beside people img */
.buddh__header-content__people p {
    margin: 0 0 0 1rem;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 38px;
    color: #FFFFFF;
    text-align: center;
}
        /* ai image styling */
.buddh__header-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}
        /* buddha image itself changing */
.buddh__header-image img{
    width: 100%;
    height: 100%;
}
        /* to run on multiple devices like tab */
@media screen and (max-width: 1050px) {
    .buddh__header {
        flex-direction: column;
    }

    .buddh__header-content {
        margin: 0 0 3rem;
    }
}
        /* like mobiles */
@media screen and (max-width: 650px) {
    .buddh__header h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .buddh__header p {
        font-size: 16px;
        line-height: 24px;
    }
        /* for people responsive to device */
    .buddh__header-content__people {
        flex-direction: column;
    }
        /* for people para responsive */
    .buddh__header-content__people p{
        margin: 0;
    }

    .buddh__header-content__input input,
    .buddh__header-content__input button {
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px) {
    .buddh__header h1 {
        font-size: 36px;
        line-height: 48px;
    }

    .buddh__header p {
        font-size: 14px;
        line-height: 24px;
    }

    .buddh__header-content__input input,
    .buddh__header-content__input button {
        font-size: 12px;
        line-height: 16px;
    }
}