.buddh__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 0.5rem 6rem;
}
/* className uses in css */
.buddh__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.buddh__navbar-links_logo {
    margin-right: 2rem;
}
/* as comparing to figma site we gave the exact precize height and weight */
.buddh__navbar-links_logo img{
    width: 62.56px;
    height: 62.02px;
}

.buddh__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.buddh__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.buddh__navbar-links_container p,
.buddh__navbar-sign p,
.buddh__navbar-menu_container p {
    color: #ffff;
    font-family: var(--font-family);    /* here we are using variable which we included inindex.css earlier Manrope*/
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}
/* for the sign up button */
.buddh__navbar-sign button,
.buddh__navbar-menu_container button{
    padding: 0.5rem 2rem;
    color: #fff;
    background: #FF4820;
    left: 1153px;
    top: 55px;
    font-weight: 400;
    line-height: 15px;
    border-radius: 5px;
}

.buddh__navbar-menu {
    margin-left: 1rem;

    display: none;
    position: relative;
}

.buddh__navbar-menu svg {
    cursor: pointer;
}

.buddh__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
}

.buddh__navbar-menu_container p {
    margin: 1rem 0;
}

.buddh__navbar-menu_container-links-sign {
    display: none;
}

@media screen and ( max-width: 1050px ) {
    .buddh__navbar-links_container {
        display: none;
    }

    .buddh__navbar-menu {
        display: flex;
    }
}

@media screen and ( max-width: 700px ) {
    .buddh__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and ( max-width: 550px ) {
    .buddh__navbar {
        padding: 2rem;
    }

    .buddh__navbar-sign {
        display: none;
    }

    .buddh__navbar-menu_container {
        top: 20px;
    }
        /* down block will help signup button show on menu bar coz that is no more avlbl in main */
    .buddh__navbar-menu_container-links-sign {
        display: block;
    }
}